.global-wrapper {
  min-height: 100%;
  padding-top: 60px;
  display: flex;
  flex-direction: column;
  flex: 1;
  overflow-x: hidden;
}
.banner-header {
  position: fixed;
  width: 100%;
  top: 0;
  height: 60px;
  z-index: 300;
  background-color: #fff;
  &.reversed {
    background-color: #222324;
  }
}
.header-wrapper {
  position: relative;
  margin: 0 auto;
  padding: 0 16px;
}
.header {
  display: flex;
  height: 60px;
  align-items: center;
  justify-content: space-between;
  flex: 1;
  z-index: 901;
  margin: 0 auto;
  max-width: 944px;
}
.header_logo {
  margin-right: 15px;
}
.navbar-brand {
  text-decoration: none;
  color: #000;
  font-weight: bold;
  &.reversed {
    color: #fff;
  }
}
.nav-button,
.nav-close {
  margin-top: -0.2em;
  overflow: hidden;
  z-index: 902;
  cursor: pointer;
  text-decoration: none;
}
.nav-button {
  display: block;
  right: 2rem;
  height: 25px;
  width: 25px;
}
.nav-button__line {
  display: block;
  width: 25px;
  height: 2px;
  background-color: #000;
  position: relative;
  opacity: 1;
  transform: rotate(0);
  transition: 0.25s ease-in-out;
  &.reversed {
    background-color: #fff;
  }
  &:nth-child(1) {
    top: 7px;
    left: 0;
  }
  &:nth-child(2) {
    top: 15px;
    right: 0;
  }
}
.nav {
  display: none;
  position: fixed;
  top: 50px;
  padding-top: 40px;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 900;
  overflow-x: hidden;
  overflow-y: auto;
  line-height: 2.4rem;
  background-color: #fff;
  text-align: center;
  &.reversed {
    background-color: #222324;
  }
  &.has-subnav::after {
    content: "";
    margin-left: 4px;
    width: 20px;
    height: 20px;
    right: -24px;
    position: absolute;
  }
}
.nav ul {
  display: block;
  position: relative;
  zoom: 1;
  list-style-type: none;
  margin: 0;
  padding: 0;
}
.nav li {
  cursor: pointer;
}
.nav a {
  &,
  &:hover,
  &:active,
  &:visited {
    text-decoration: none;
  }
}
.nav-link,
.nav-sublink {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
}
.nav-link {
  padding: 12px 16px 12px 32px;
  margin: 0 16px 1px 16px;
  font: normal 26px/36px "Barlow", sans-serif;
  color: #222324;
  &.reversed {
    color: #fff;
  }
  &.has-subnav {
    margin-right: 20px;
  }
}
.for-desktop {
  display: none;
}
.nav-submenu {
  display: none;
  background-color: rgba(255, 255, 255, 0.95);
  &.reversed {
    background-color: rgba(34, 35, 36, 0.95);
  }
}
.nav-sublink {
  font: normal 20px/28px "Barlow", sans-serif;
  padding: 12px 16px 12px 68px;
  color: #222324;
