.footer {
  display: grid;
  padding: 260px 0 24px 0;
  position: relative;
  z-index: 1;
  grid:
    ". ctitle . "
    ". contact . "
    " . . ."
    ". ntitle . "
    ". nav . "
    ". . ."
    ". social ."
    ". . . "
    ". logo ."
    ". . ."
    ". privacy .";
  grid-template-columns: auto 190px auto;
  grid-template-rows: auto auto 24px auto auto 36px auto 80px auto 40px 40px;
  text-align: center;
  @media (min-width: 768px) {
    grid:
      ". ctitle . ntitle . logo ."
      ". contact . nav . . ."
      ". . . . . . ."
      ". social . . . . ."
      ". . . . . . ."
      "privacy privacy privacy privacy privacy privacy privacy";
    grid-template-columns: 16px auto 80px auto 80px auto 16px;
    grid-template-rows: auto auto 48px auto 40px 40px;
    text-align: left;
  }
  @media (min-width: 992px) {
    grid:
      " ctitle . ntitle . logo "
      " contact . nav . . "
      " . . . . . "
      " social . . . . "
      " . . . . . "
      "privacy privacy privacy privacy privacy";
    grid-template-columns: 200px 80px 200px 80px auto;
    grid-template-rows: auto auto 48px auto 40px 40px;
  }
}
.footer-no-cta {
